.first-screen {
  background-image: url("../../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position-x: center;

  @media (min-resolution: 2dppx) {
    background-image: url("../../img/index-background-mobile@2x.jpg");
    background-size: 305px auto;
  }

  @media (min-width: $tablet-width) {
    background-image: none;
    background-color: $bg-color;
  }

  @media (min-width: $desktop-width) {
    min-height: 696px;

    &::before {
      content: "";

      background-image: url("../../img/index-background-desktop@1x.jpg");
      background-repeat: no-repeat;
      position: absolute;
      width: 50vw;
      top: 0;
      right: 0;
      height: 100%;

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/index-background-desktop@2x.jpg");
        background-size: 100% auto;
      }
    }
  }
}

.first-screen__title {
  font-family: "Oswald-Regular", "Arial", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: normal;
  color: $secondary-title-color;
  text-align: center;

  margin: 0 auto;
  padding-top: 29px;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;
    color: $title-color;
    text-align: left;
    margin-left: 91px;

    width: 60%;
  }

  @media (min-width: $desktop-width) {
    width: 36%;
  }
}

.first-screen__slogan {
  font-size: 14px;
  line-height: 14px;
  font-weight: normal;
  color: #ffffff;
  text-align: center;

  display: block;
  padding-top: 19px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
    color: #111111;
    text-align: left;

    padding-top: 33px;
    margin-left: 91px;
  }
}

.first-screen__image-feed-pack {
  display: block;
  padding-top: 29px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    order: 2;
  }

  @media (min-width: $desktop-width) {
    position: relative;
    top: -225px;
    left: 106px;
  }
}

.first-screen__link {
  display: inline-block;
  font-size: 14px;
  width: 100%;
  line-height: 18px;
  color: #ffffff;

  &:focus,
  &:hover {
    background-color: #5eaa2f;
    outline: none;
  }

  &:active {
    color: #ffffff;
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    position: absolute;
    margin: -572px 90px;
    width: 32%;
  }

  @media (min-width: $desktop-width) {
    width: 17%;
    margin: -470px 90px;
  }
}
